import { motion, useMotionValue, useTransform } from "framer-motion";
import styled from "styled-components";
import { CardData } from "../../helpers/getToKnowMeHelpers";
import { theme } from "../Theme/sc-theme";

interface DraggableCardProps {
  cardInfo: CardData;
  cards: CardData[];
  onAnswer: (id: number, answer: boolean) => void;
}

export const DraggableCard = ({
  cardInfo,
  cards,
  onAnswer,
}: DraggableCardProps) => {
  const x = useMotionValue(0);

  const rotateRaw = useTransform(x, [-150, 150], [-18, 18]);
  const opacity = useTransform(x, [-150, 0, 150], [0, 1, 0]);

  const isFront = cardInfo.id === cards[cards.length - 1].id;

  const rotate = useTransform(() => {
    const offset = isFront ? 0 : cardInfo.id % 2 ? 6 : -6;

    return `${rotateRaw.get() + offset}deg`;
  });

  const handleDragEnd = () => {
    // if info x.get() < 0, swiped to the left (false), else swiped to the right (true)
    if (Math.abs(x.get()) > 100) {
      const answer = x.get() > 0;
      onAnswer(cardInfo.id, answer === cardInfo.answer);
    }
  };

  return (
    <StyledCard
      style={{
        x,
        opacity,
        rotate,
        transition: "0.125s transform",
        boxShadow: isFront
          ? "0 20px 25px -5px rgb(0 0 0 / 0.5), 0 8px 10px -6px rgb(0 0 0 / 0.5)"
          : undefined,
      }}
      animate={{
        scale: isFront ? 1 : 0.98,
      }}
      drag={"x"}
      dragConstraints={{
        left: 0,
        right: 0,
      }}
      onDragEnd={handleDragEnd}
    >
      {cardInfo.question}
    </StyledCard>
  );
};

const StyledCard = styled(motion.div)`
  width: 200px;
  height: 300px;
  background-color: #fff;
  border: 3px solid ${theme.colors.orange};
  transform-origin: bottom;
  border-radius: 0.5rem;
  object-fit: cover;
  grid-row: 1;
  grid-column: 1;
  background-color: ${theme.colors.blue};
  opacity: 0.8;
  background-image: radial-gradient(${theme.colors.red} 0.5px, #fff 0.5px);
  background-size: 10px 10px;
  display: flex;
  align-items: center;
`;
